    export default {
        data() {
            return {
                toUserMsg:'',
				bosschecked:'',
				staffchecked:'',
				windowHeight:''
            };
        },
        methods: {
            toIndex:function(){
				let _this=this;
				
				uni.navigateTo({
				    url: _this.toUserMsg
				});
			},
			toBoss:function(){
				this.toUserMsg='';
				let url='/pages/userMsg/boss';
				this.toUserMsg=url
				
				uni.setStorageSync('userType',2)
				//改变选中元素边框
				this.staffchecked=''
				this.bosschecked='border:1px solid rgb(91,216,147)';
			},
			toStaff:function(){
				this.toUserMsg=url;
				let url='/pages/userMsg/staff';
				this.toUserMsg=url
				
				uni.setStorageSync('userType',3)
				
				this.bosschecked='';
				this.staffchecked='border:1px solid rgb(91,216,147)'
			}
        },
        onLoad() {//默认加载
		    this.windowHeight = this.$store.state.windowHeight
            
        }
    }
